
* {
  padding: 0;
  margin: 0;
}
html {
  min-height: 100%;
  background: url(../../static/img/the-fishermen-3039591_1920.a0afe27.jpg) no-repeat fixed left top / cover;
  overflow: hidden;
}
.bg-f {
  position: fixed;
  bottom: 10px;
  left: 10px;
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
  color: #f0f0f0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-top: 60px;
}

.simple-draggable .center {
  text-align: center;
}
.simple-draggable .draggable {
  width: 100px;
  height: 100px;
  position: absolute;
  cursor: move;
}
.simple-draggable .only {
  top: 100px;
  left: 100px;
  background-color: #41b883;
  color: #35495d;
}
.simple-draggable .clone {
  top: 100px;
  right: 100px;
  background-color: #35495d;
  color: #41b883;
}
.simple-draggable .frame {
  width: 900px;
  height: 500px;
  position: absolute;
  top: 300px;
  left: 100px;
  border: 1px #41b883 solid;
}
.simple-draggable .frame-node {
  background-color: #35495d;
  top: 144px;
  left: 305px;
  color: #41b883;
}
.simple-draggable .foucs {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: #fff;
}
